export default {
	data() {
		return {
			type:'password',
			isuser:false,  // 用户名是否可用
			isemail:false, // 邮箱是否被占用
			look:false,     // 是否显示密码
			invalid:false, // 邮箱是否符合
			useremploy:false,  // 是否被占用
			emailemploy:false, // 邮箱是否被占用
			lookurl:'../../static/images/unlook.png',
			email:'',      // 邮箱
			user:'',       // 用户名
			psw:'',        // 密码			
			isok:false,
		};
	},
	methods:{
		//密码显示隐藏
		looks:function(){
			if(this.look){
				this.type = 'password';
				this.lookurl = '../../static/images/unlook.png'
			}else{
				this.type = "text";
				this.lookurl = '../../static/images/look.png'
			}
			this.look = !this.look;
		},
		
		//判断是否是邮箱格式
		isEmail:function(e){
			this.email = e.detail.value;
			let reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
			if(this.email.length > 0){
				if(reg.test(this.email)){
					this.invalid = false;
				}else{
					this.invalid = true;
				}
			}
			this.isOk();
		},
		//获取用户名
		getUser:function(e){
			this.user = e.detail.value
			this.isOk();
		},
		getPsw:function(e){
			this.psw = e.detail.value
			this.isOk();
		},
		//判断是否可以注册了
		isOk:function(){
			if(this.isuser && this.isemail && this.psw.length>5){
				this.isok = true;
			}else{
				this.isok = false;
			}
		},
		//返回登录页面
		toSignIn:function(){
			uni.navigateBack({
				delta:1 // 返回一层
			})
		}
	}
};
